import React, {Component} from 'react'
import {connect} from 'react-redux'
import {loadCatalog, addCart} from 'Actions'
import ProductListItem from 'Components/ProductListItem'
import CircularProgress from 'material-ui/CircularProgress'
import PropTypes from 'prop-types'

class Catalog extends Component{
	componentDidMount(){
		if( !this.props.loaded && !this.props.loading )
			this.props.loadCatalog()
	}
	render(){

		if( this.props.error )
			return <h1>Server error</h1>

		const {loading, loaded, phones, addCart} = this.props

		function getBody(){
			if( loading && !loaded ) return <CircularProgress size={60} thickness={7} />
			return(
				<div className="row">
					{phones.map((phone) => {
						return(
							<div className="col-md-4" key={phone.id}>
								<ProductListItem 
									id={phone.id}
									name={phone.name}
									link={phone.id}
									price={phone.price}
									srcImg={phone.image}
									funcAddCart={ addCart }
								/>
							</div>
						)
					})}
				</div>
			)
		}
		return(
			<div>
				{getBody()}
			</div>
		)
	}
}

Catalog.propTypes = {
	addCart: PropTypes.func,
	loadCatalog: PropTypes.func,
	loaded: PropTypes.bool,
	loading: PropTypes.bool,
	phones: PropTypes.array
}

const mapDispatchToProps = (dispatch) => {
	return({
		loadCatalog: () => dispatch( loadCatalog() ),
		addCart: (price, id) => dispatch( addCart(price, id) )
	})
}
const mapStateToProps = (state) => {

	function filtered(phones, filter){
		const {search, brands} = filter

		const arrBrands =  Object.keys(brands).reduce( (arr, brand) => {
			if( brands[brand] )
				arr.push(brand)
			return arr
		}, [])

		function checkCategory( categoryId ){
			if( !arrBrands.length || arrBrands.indexOf( categoryId ) > -1 )
				return true
			else
				return false
		}

		return phones.reduce( (acc,product) => {
			if(product.name.toLowerCase().indexOf(search.toLowerCase()) > -1 && checkCategory(product.categoryId) )
				acc.push(product)
			return acc
		}, [])
	}

	return{
		loading: state.catalog.loading,
		loaded: state.catalog.loaded,
		error: state.catalog.error,
		phones: filtered(Object.values(state.catalog.phones), state.filter)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Catalog)


